<template>
  <div>
    <div class="jumbotron px-5 py-4">
      <h2 class="text-center mt-3">Show Log</h2>
      <p class="text-secondary text-center mb-5">This section will show you your personal log so you can see the evolution of your trainings.</p>
      <form action="api/exercise/add" @submit.prevent="showLog">
        <div class="form-group row">
          <div class="input-group">
            <div class="input-group-prepend col-sm-4 col-md-2 px-0">
              <label for="username" class="input-group-text w-100">Username</label>
            </div>
            <input
              id          = "username"
              class       = "col-sm-8 col-md-10 form-control"
              type        = "text"
              v-model     = "username"
              @input      = "resetAlerts"
              minlength   = "3"
              maxlength   = "30"
              placeholder = "Type your username, please"
              required
              autofocus
            />
          </div>
        </div>
        <div class="form-group row">
          <div class="input-group">
            <div class="input-group-prepend col-sm-4 col-md-2 px-0">
              <label for="from" class="input-group-text w-100">From</label>
            </div>
            <datepicker
              id            = "from"
              wrapper-class = "col-sm-8 col-md-10 px-0"
              input-class   = "w-100 form-control"
              v-model       = "from"
              @input        = "resetAlerts"
              placeholder   = "Choose a starting date"
              typeable
            />
          </div>
        </div>
        <div class="form-group row">
          <div class="input-group">
            <div class="input-group-prepend col-sm-4 col-md-2 px-0">
              <label for="to" class="input-group-text w-100">To</label>
            </div>
            <datepicker
              id            = "to"
              wrapper-class = "col-sm-8 col-md-10 px-0"
              input-class   = "w-100 form-control"
              v-model       = "to"
              @input        = "resetAlerts"
              placeholder   = "Choose an end date"
              typeable
            />
          </div>
        </div>
        <div class="form-group row">
          <div class="input-group">
            <div class="input-group-prepend col-sm-4 col-md-2 px-0">
              <label for="limit" class="input-group-text w-100">Limit</label>
            </div>
            <input
              id          = "limit"
              class       = "col-sm-8 col-md-10 form-control"
              type        = "number"
              v-model     = "limit"
              @input      = "resetAlerts"
              minlength   = "3"
              maxlength   = "30"
              placeholder = "How many results to show"
            />
          </div>
        </div>
        <div class="form-group row mt-5">
          <div class="col-sm-12 text-center mx-0 px-0">
            <button type="submit" class="btn btn-block btn-primary">Search</button>
          </div>
        </div>
      </form>
      <Alert :error="error" />
    </div>
    <transition name="fade">
      <div class="card-deck text-center p-3" v-show="results">
        <p class="col-12 h4 mb-5 pb-3 log-heading">This is what your log looks like</p>
        <div
          v-for = "result in this.results"
          :key  = "result._id"
          class = "col-sm-12 col-md-6 col-lg-4"
          >
          <div class="card border-light mb-3">
            <div class="card-header bg-primary text-light"><strong>{{ result.date }}</strong></div>
            <div class="card-body">
              <p class="lead"><strong>{{ result.name }}</strong></p>
              <small>Done in <strong>{{ result.time }}</strong> minutes</small>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import Alert      from '../components/Alert';
import Datepicker from 'vuejs-datepicker';
import axios      from 'axios';

export default {
  name: 'SignUp',
  components: { Datepicker, Alert },
  data( ) {
    return {
      username: '',
      from    : '',
      to      : '',
      limit   : '',
      error   : '',
      results : null
    }
  },
  methods: {
    resetAlerts ( ) {
      this.error = this.results = null; 
    },
    showLog ( e ) {
      e.preventDefault( );
      this.resetAlerts( );

      const query = { username: this.username };
        if ( this.from  ) query.from  = this.from;
        if ( this.to    ) query.to    = this.to;
        if ( this.limit ) query.limit = this.limit;

      axios
        .get( `/api/exercise/log`, { params: query } )
        .then( data => {
          if ( data.data.length === 0 )
            return this.error = `Sorry. ${this.username} didn't log anything yet.`
          this.results = [];
          data.data.map( item => {
            let date = new Date( item.date )
              .toLocaleDateString(
                'en-US',
                { weekday: 'short', year: 'numeric', month: 'long', day: 'numeric' }
              );
            this.results.push( {
              username: item.username,
              name    : item.name,
              time    : item.time,
              date    : date
            } )
          } );
        } )
        .catch( error => {
          if ( error.response )
            return this.error = error.response.data.error;
          this.error = 'Oops! Something went wrong.'
        } );
    }
  }
}
</script>